import React, {useEffect} from 'react';
import {connect} from "react-redux";
import {ClosedStatusCount, getAllTicket, OpenStatusCount, PendingStatusCount} from "../redux/action/TicketsAction";

const SubNavbarMenu = (props) => {
    useEffect(()=>{
        props.OpenStatusCount()
        props.ClosedStatusCount()
        props.PendingStatusCount()
        props.getAllTicket()
    }, [])

    return (
        <div className='sub-navbar-menu'>


       <div className="sub-navbar-menu-child">
           <div className="tickets-count">
               <div className="gm-father">
                   <img src="/img/icon/gm1.png" alt=""/>
               </div>

               <div>
                   <h1>
                       {props.AllCount.count}





                       <span>+
                           {
                               props.AllCount.data?.filter(item => !item.is_read).length
                           }
                       </span>
                   </h1>
                   <p>All tickets</p>
               </div>
           </div>

           <div className="tickets-count">
               <div className="gm-father">
                   <img src="/img/icon/gm2.png" alt=""/>
               </div>

               <div>
                   <h1>
                       {props.openCount.count}
                       <span>+
                           {
                               props.openCount.data?.filter(item => !item.is_read).length
                           }
                       </span>
                   </h1>
                   <p>Open</p>
               </div>
           </div>


           <div className="tickets-count">
               <div className="gm-father">
                   <img src="/img/icon/gm3.png" alt=""/>
               </div>

               <div>
                   <h1>
                       {props.PendingCount.count}

                       <span>+
                           {
                               props.PendingCount.data?.filter(item => !item.is_read).length
                           }
                       </span>
                   </h1>
                   <p>Pending</p>
               </div>
           </div>


           <div className="tickets-count">
               <div className="gm-father">
                   <img src="/img/icon/gm4.png" alt=""/>
               </div>

               <div>
                   <h1>
                       {props.ClosedCount.count}

                       <span>+
                           {
                               props.ClosedCount.data?.filter(item => !item.is_read).length
                           }
                       </span>
                   </h1>
                   <p>Closed</p>
               </div>
           </div>

       </div>

        </div>
    );
};



const mapStateToProps=(state)=>{
    return{
        openCount: state.ticketData.openCount,
        PendingCount: state.ticketData.PendingCount,
        ClosedCount: state.ticketData.ClosedCount,
        AllCount: state.ticketData.AllCount,
    }
}

export default connect(mapStateToProps, {OpenStatusCount, PendingStatusCount,getAllTicket, ClosedStatusCount})(SubNavbarMenu);