import {UPDATE_STATE} from "../actionTypes/createAgentType";


const initialState ={

    getTickets: [],
    getOpenTickets: [],
    getLastDaysTickets: [],
    getTicketsToHandle: [],
    ticketStatusList: [],
    commitList: [],
    ticketInfoObject: {},
    btc: {},
    boc: {},
    bot: {},
    openCount: [],
    ClosedCount: [],
    PendingCount: [],
    AllCount: [],
    selectedCommitId: {},

}
export const TicketsReducer = (state= initialState, action) => {
    switch (action.type){
        case UPDATE_STATE:
            return {...state, ...action.payload}
        default: return state;
    }
}
