import React, {useEffect} from 'react';
import {Link, NavLink} from "react-router-dom";
import {connect} from "react-redux";
import {
    filterStatusFunction,
    getAllTicket,
    getAllTicketToHandle,
    getMyOpenTicket,
    getStatusList, getTicketsSevenDays
} from "../redux/action/TicketsAction";


const SideBarMenus = (props) => {




    var date = new Date();
    date.setDate(date.getDate() - 7);

    var finalDate = date.getFullYear()+'-'+ (date.getMonth()+1) +'-'+ date.getDate();




    let currentDate = new Date()
    let day = currentDate.getDate()
    let month = currentDate.getMonth() + 1
    let year = currentDate.getFullYear()

    let today = year + "-" + month + "-" + day



    useEffect(()=>{
        props.getStatusList()
        props.getMyOpenTicket()
        props.getAllTicketToHandle()
        props.getAllTicket()
        props.getTicketsSevenDays(finalDate, today)

    }, [])




    const filterByStatus = (data) =>{

        props.filterStatusFunction(data)

    }


    const getAllStatusList =()=>{
        props.getAllTicket()
    }

    return (

        <div className="side-bar-menus">
            <div className="side-bar-menus-child">

                <h1 className="open-sans-extra-bold">Tickets</h1>


                <Link to='/tickets/new-ticket' className="new-ticket open-sans-extra-bold">


                    <img src="/img/icon/plus.png" alt="plus icon"/>
                    New ticket
                </Link>

                <div className="ticket-search">
                    <img src="/img/icon/loupe.png" alt=""/>
                    <input type="text" placeholder="Search..."  />

                </div>

                <div className="menus">
                    <Link to="/tickets/all-tickets" className="    w-100">
                        <button  onClick={()=>getAllStatusList()} className=" p-0 "><span>All tickets</span>
                            <span className="">
                            {
                            props.AllCount.data?.filter(item=>item).length
                            }
                        </span></button>

                    </Link>

                    <Link to="/tickets/tickets-to-handle"><span>Tickets to handle</span><span>
                        {
                        props.btc.data?.filter(item=>item).length
                        }
                    </span></Link>
                    <Link to="/tickets/open-tickets" ><span>My open tickets</span><span>
                          {
                              props.boc.data?.filter(item=>item).length
                          }
                    </span></Link>



                    <h4>My views</h4>
                    <Link to="/tickets/last-days" ><span>My tickets in last 7 days</span><span>
                          {
                              props.bot.data?.filter(item=>item).length
                          }
                    </span></Link>



                    <h4>Statuses</h4>


                    {
                        props.ticketStatusList.map(item=>(
                            <button onClick={()=>filterByStatus(item.name)} ><span>{item.name}</span> </button>

                        ))
                    }

                </div>
            </div>

        </div>
    );
};


const mapStateToProps =(state)=>{
    return{
        ticketStatusList: state.ticketData.ticketStatusList,
        btc: state.ticketData.btc,
        boc: state.ticketData.boc,
        bot: state.ticketData.bot,
        AllCount: state.ticketData.AllCount,
        getTicketsToHandle : state.ticketData.getTicketsToHandle,
        getOpenTickets : state.ticketData.getOpenTickets,
        openCount: state.ticketData.openCount,
        PendingCount: state.ticketData.PendingCount,
        ClosedCount: state.ticketData.ClosedCount,

    }
}
export default connect(mapStateToProps, {getStatusList,getTicketsSevenDays,getAllTicketToHandle,getMyOpenTicket, getAllTicket, filterStatusFunction})(SideBarMenus);