import React, {useEffect} from 'react';
import {changeTicketStatus, getCommit, getAllTicketToHandle, getStatusList} from "../redux/action/TicketsAction";
import {connect} from "react-redux";
import {Link, useHistory} from "react-router-dom";



const TicketsHandleTable = (props) => {

    useEffect( () =>{
        props.getAllTicketToHandle()
        props.getStatusList()
        props.changeTicketStatus()

    }, [])

    let history = useHistory();
    const changeStatusFunction =(data, values)=>{
        props.changeTicketStatus(data, values)

    }



    const  visitCommit =(values, events)=>{
        props.getCommit(values, history)
    }
    return (
        <div className='main-table-for-tickets'>



            <table>

                <tr className="main-table-header">
                    <th><input type="checkbox" /></th>
                    <th>
                        Requester
                    </th>
                    <th>
                        Subject
                    </th>
                    <th>
                        Agent
                    </th>
                    <th>
                        Status
                    </th>
                    <th>
                        Last message
                    </th>
                    <th>

                    </th>
                    <th></th>
                </tr>






                {
                    props.getTicketsToHandle.map((item, index) => (
                        <tr onClick={()=>visitCommit(item.id)} style={{cursor: "pointer"}}>
                            <td>
                                <input type="checkbox" />
                            </td>
                            <td className="requester-name">
                                <h4>{item.client_full_name}</h4>
                                <span>{item.sender_name}</span>
                            </td>
                            <td className="subject-name">

                                {item.priority === 1 ? <img src="/img/icon/open.png" alt="" /> :  item.priority === 2 ? <img src="/img/icon/fast.png" alt="" /> :
                                    item.priority === 0 ? <img src="/img/icon/onHold.png" alt="" /> : item.priority === -1 ? <img src="/img/icon/close.png" alt="" />          : item.priority === -2 ?  "iconnnn"   : ''                         }


                                {" " +  item.subject}
                            </td>

                            <td>
                                {item.receiver_name}
                            </td>
                            <td><span className="open">
                       {item.ticket_status_name}
                    </span></td>
                            <td>
                                {
                                    item.date_created.slice(0, 10)
                                }
                            </td>
                            <td className="all-tickets-event-menu-father dropdown">
                                <button className="all-tickets-event dropbtn">
                                    <img src="/img/icon/verDots.png" alt=""/>
                                </button>



                                <div className="all-tickets-event-menu dropdown-content">


                                    {
                                        props.ticketStatusList.map(item2 =>(
                                            <button  onClick={()=> changeStatusFunction(item.id, item2.id)}>{item2.name}</button>
                                        ))
                                    }

                                </div>


                            </td>
                            {/*<td> </td>*/}

                        </tr>

                    ))
                }








            </table>


        </div>
    );
};

//

const  mapStateToProps = (state) =>{
    return{

        getTickets : state.ticketData.getTickets,
        getTicketsToHandle : state.ticketData.getTicketsToHandle,
        ticketStatusList : state.ticketData.ticketStatusList,

    }
}

export default connect(mapStateToProps, {getAllTicketToHandle, getCommit, changeTicketStatus , getStatusList})(TicketsHandleTable);